import React, { Suspense, lazy, useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, BrowserRouter } from 'react-router-dom';
import { onAuthStateChanged } from 'firebase/auth';
import { auth } from '../Utils/Firebase';
import AuthenticationUser from './AuthenticationUser';
import Home from './Home';


const Body = () => {
  const [user, setUser] = useState(null);
  const MoreMovies = lazy(() => import('./MoreMovie'))
  const WatchList = lazy(() => import('./WatchList'))
  const SearchMovie = lazy(() => import('./SearchMovie'))
  const WatchMovie = lazy(() => import('./WatchMovie'))

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setUser(user);
    });

    return () => unsubscribe();
  }, []);

  return (
    <BrowserRouter basename='/Filmy'>
      <Routes>
        <Route path="/Filmy" element={<ConditionalAuthenticationUser user={user} />} />
        <Route path="/home" element={user ? <Home /> : <Navigate to="/" />} />
        <Route path="/watchlist" element={user ? <Suspense fallback={<h1>loading...</h1>} > <WatchList /> </Suspense> : <Navigate to="/Filmy" />} />
        <Route path="/moremovies" element={user ? <Suspense fallback={<h1>loading...</h1>} > <MoreMovies /> </Suspense> : <Navigate to="/Filmy" />} />
        <Route path="/searchmovie/:id" element={user ? <Suspense fallback={<h1>loading...</h1>} > <SearchMovie /> </Suspense> : <Navigate to="/Filmy" />} />
        <Route path="/watchmovie/:id" element={user ? <Suspense fallback={<h1>loading...</h1>} > <WatchMovie /> </Suspense> : <Navigate to="/Filmy" />} />
      </Routes>
    </BrowserRouter>
  );
};

const ConditionalAuthenticationUser = ({ user }) => {
  return user ? <Navigate to="/home" /> : <AuthenticationUser />;
};

export default Body;


